import U from './utils';

class Watermark {
  constructor(el, options) {
    if (options === undefined) {
      if (U.isNode(el)) {
        this.set();
        this.mount(el);
      } else {
        this.set(el);
      }
    } else {
      this.set(options);
      this.mount(el);
    }

    /* this.el = document.querySelector(el); // èç¹
    this.textArray = textArray; // æ°´å°æ°ç»
    this.font = font; // å­ä½æ ·å¼ eg. '26px serif'; æå®å­ä½ä¹ååé¢çfontsize fontfamilyå¤±æ
    this.fontsize = fontsize; // å­å·
    this.fontFamily = fontFamily; // å­ä½
    this.width = width; // ç»å¸å®½åº¦ï¼ ä¸æ¯æå®å¶
    this.height = height; // ç»å¸é«åº¦ï¼ ä¸æ¯æå®å¶
    this.padding = padding; // æ°´å°è¾¹è·
    this.lineHeight = lineHeight; // æ°´å°è¡é«
    this.rotate = rotate; // æè½¬è§åº¦ 0 - 90' ï¼å¼§åº¦è¡¨ç¤º
    this.fontScale = fontScale; // åè§å¨è§å­ç¬¦å®½åº¦æ¯å¼
    this.color = color; */
  }

  /**
   * [calcute ctx -> draw canvs -> render]
   * @return {[type]} [description]
   */
  draw() {
    this.init();
    this.drawCanvas();
    this.render();
    this.observe();
  }

  mount(el) {
    if (typeof el === 'string') {
      this.$el = document.querySelector(el);
    } else {
      this.$el = el;
    }
    this.draw();
    return this;
  }

  set(options = {}) {
    // eslint-disable-next-line no-param-reassign
    options = Object.assign(options, {
      textArray: ['example'],
      fontSize: 26,
      fontFamily: 'serif',
      padding: 25,
      lineHeight: -1,
      rotate: Math.PI / 6,
      fontScale: 0.5,
      color: '#eeeeee',
    });
    this.options = options;
    return this;
  }


  init() {
    this.initFont(); // å¿é¡»ååå§åå­ä½
    this.initSize(); // ç¶åè®¡ç®ç»å¸å¤§å°
  }

  /**
   * åå§åå­ä½ï¼è®¡ç®è¡é«
   */
  initFont() {
    const options = this.options;
    this.options.font = `${options.fontSize}px ${options.fontFamily}`;

    if (options.lineHeight === -1) {
      this.options.lineHeight = 1.25 * options.fontSize;
    }
  }

  /**
   * è®¡ç®ç»å¸å¤§å°
   */
  initSize() {
    const options = this.options;

    // max length of text array
    const maxLength = options.textArray.reduce((max, current) => {
      const currentLength = U.length(current, options.fontScale);
      return currentLength > max ? currentLength : max;
    }, 0);

    // åå®¹å®½é«
    const W = maxLength * options.fontSize + options.padding * 2;
    const H = options.textArray.length * options.lineHeight + options.padding * 2;

    const a = Math.abs(options.rotate); // è§åº¦
    // ç»å¸å®½é«context width => cW, context height => cH
    const ctxW = H * Math.sin(a) + W * Math.cos(a);
    const ctxH = H * Math.cos(a) + W * Math.sin(a);

    this.contentWidth = W;
    this.contentHeight = H;
    this.ctxWidth = Math.floor(ctxW);
    this.ctxHeight = Math.floor(ctxH);
  }

  drawCanvas() {
    const canvas = document.createElement('canvas');
    canvas.width = this.ctxWidth;
    canvas.height = this.ctxHeight;
    if (canvas.getContext) {
      const ctx = canvas.getContext('2d');
      const options = this.options;

      ctx.translate(...this.origin(this.rotate));
      ctx.rotate(this.rotate);
      ctx.textAlign = 'start';
      ctx.textBaseline = 'top';
      ctx.font = options.font;
      ctx.fillStyle = options.color;
      const H = this.contentHeight; // åå®¹é«åº¦
      const a = Math.abs(options.rotate); // æè½¬è§åº¦
      const offsetX = H * Math.cos(a) * Math.sin(a);

      /**
       * ç¬¬åè±¡é
       * offsetY = H * Math.sin(a) * Math.sin(a)
       *
       * ç¬¬ä¸è±¡é
       * offsetX = H * Math.cos(a) * Math.cos(a)
       *
       * å æ­¤ï¼å½ -PI/2 < rotate < PI/2 æ¶
       * (PI/2 + rotate) % PI/2 çå¼è®¡ç®æ­£å¼¦å¼ï¼åå¥½ç¬¦åä¸å¼
       * ä¾å¦ï¼roate = 30, (90 + 30) % 90 = 30, sin(30) = 1/2
       * rotate = -30, (90 + (-30)) % 30 = 60, cos(60) = sin(30)
       */
      const halfPI = Math.PI / 2;
      const tmp = (halfPI + options.rotate) % halfPI;
      const offsetY = -H * Math.sin(tmp) * Math.sin(tmp);
      options.textArray.forEach((text, i) => {
        const x = offsetX + options.padding;
        const y = i * options.lineHeight + offsetY + options.padding;
        ctx.fillText(text, x, y);
      });
      this.canvas = canvas;
    }
  }

  /**
   * è·ååæ åç¹
   * @param {*} rotate è®¾ç½®çæ°´å°æè½¬è§åº¦
   */
  origin(rotate) {
    let origin = [0, 0]; // ç¬¬åè±¡éï¼å®ä¹å·¦ä¸è§ä¸ºåæ åç¹

    if (rotate < 0 && rotate > -Math.PI / 2) { // ç¬¬ä¸è±¡éï¼å®ä¹å·¦ä¸è§ä¸ºåæ åç¹
      origin = [0, this.height];
    }
    return origin;
  }

  /**
   * ç»å¶ãæ·»å èæ¯å°æå®èç¹ä¸
   */
  render() {
    // const dataUrl = this.dataUrlVal || this.dataUrl();
    const dataUrl = this.canvas.toDataURL();
    this.$el.style.background = `url(${dataUrl})`;
    this.background = this.$el.style.background;
  }


  /**
   * æ£æµèç¹èæ¯æ¯å¦åçååã
   */
  observe() {
    if (this.observer) {
      // remove observer
    }

    const observer = new MutationObserver((mutations, observer) => {
      mutations.forEach((mutation) => {
        const target = mutation.target;
        // æ£æµèæ¯æ¯å¦ä¸è®¾ç½®çæ°´å°ç¸å
        if (target.style.background !== this.background) {
          target.style.background = this.background;
        }
      });
    });

    observer.observe(this.$el, {
      attributes: true,
      childList: false,
      attributeFilter: ['style'],
    });
    this.observer = observer;
  }
}

export default Watermark;
